  export default {
    props: {
      msg: {
        default: ''
      }
    },
//    
    methods: {
      btnClickEvent: function () {
        alert(this.msg)
//      
        this.$emit('btnCaaa')
      }
    }
  }
</script>

<style scoped>
  @import './css/qui-btn.import.css';
</style>



